export default {
	//components: {},
	destroyed() { 

		}, 
	data() {
		return {
			User_Value:"",
			PassWord_Value:"",
			error_User_remind:true,
			User_Error:"",
			error_Pass_remind:true,
			Pass_Error:"",
		}
	},
	onLoad() {
		uni.setStorage({
			key: 'userName',
			data: '',
		});
	},
	onShow(){		

	},
	methods: {			
		check_User_length(e){
			var UserName = e.detail.value;
			if(UserName.length < 1)
			{
				this.User_Error = '请输入姓名';
			}
			else
			{
				this.User_Error = '';
			}
		},
		
		check_Pass_length(e){
			var Pass = e.detail.value;
			if(Pass.length < 1)
			{
				this.Pass_Error = '请输入密码';
			}
			else
			{
				this.Pass_Error = '';
			}
		},
		
		//登录
		Login()
		{
			//var Getopenid= uni.getStorageSync('OpenId');
			var User_Value = this.User_Value.trim();
			var Pass_Value = this.PassWord_Value.trim();
			if(User_Value.length < 1)
			{
				this.User_Error = '请输入姓名';
			}
			else
			{
				this.User_Error = '';
			}
			
			if(Pass_Value.length < 1)
			{
				this.Pass_Error = '请输入密码';
			}
			else
			{
				this.Pass_Error = '';
			}
			
			if(this.error_Pass_remind == false  || this.error_User_remind  == false)
			{
				return;
			}
			
			var Url =  this.$websiteUrl+'/api/mlogin/post';
			uni.request({
			//	url: 'http://open.orders.hscode.net:8081/index.php?r=test/testsave',
				url:Url,
				method: 'POST',
				data: {username:User_Value,password:Pass_Value},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:'+res);
					//uni.hideLoading(); 
					if(res.data.code == '200')
					{
						uni.setStorage({
							key: 'userName',
							data: User_Value,
						});
						
						uni.setStorage({
							key: 'token',
							data: res.data.result,
						});
						
						console.log(uni.getStorageSync('userName'));
						uni.showToast({
							title: '成功',
							icon:'none',
							mask: false,
							duration: 2800,
							success:function(){
								console.log(User_Value);
								uni.navigateTo({
								   url:'/pages/backend/index1'
								});
							}
						});
						

					}
					else
					{
						uni.showToast({
							title: res.data.text,
							icon:'none',
							mask: false,
							duration: 12800
						});
					}
				},
				fail: failres => {
					console.log(failres);
					uni.showToast({
						title: '提交异常',
						icon:'none',
						mask: false,
						duration: 2800
					});
					
				},
				complete: () => {}
			});
		}
	}
}
